import React from 'react';
import ReactDOM from 'react-dom';
import { ChildClass } from './child';
import './index.css';

class ParentClass extends React.Component {
	constructor(props) {
		super(props);

		this.state = { astronauts: 0 };

		this.handleClick = this.handleClick.bind(this);
	}

	handleClick() {
		const url = "http://api.open-notify.org/astros.json";
		const Httpreq = new XMLHttpRequest();
		Httpreq.open("GET",url,false);
		Httpreq.send(null);
		var json_obj = JSON.parse(Httpreq.responseText);
		var astros = json_obj.number;
		console.log(json_obj.number);

		this.setState(
			{ astronauts: astros }
		);
	}

	render() {
		return (
			<ChildClass astros={this.state.astronauts} onClick={this.handleClick} />
		)
	}
}

ReactDOM.render(<ParentClass />, document.getElementById('root'));


